import React, { useState, useRef, useEffect } from 'react';
// import scanIcon from "../../img/scan.png";
import { Box, Button } from "@material-ui/core";
import Model from "./Model";
import { BrowserMultiFormatReader } from '@zxing/library';
import "./index.css";

const Scan = ({ handleResult,open }) => {
    const videoRef = useRef(null);
    const streamRef = useRef(null);
    const codeReader = useRef(null);

    useEffect(() => {
        if (open) {
            startScanner();
        }
        return () => {
            stopScanner();
        };
    }, [open]);
    const startScanner = async () => {
        try {
            const constraints = {
                video: {
                    facingMode: 'environment', // 
                },
                audio: false,
            };
            const stream = await navigator.mediaDevices.getUserMedia(constraints);
            streamRef.current = stream;
            if (videoRef.current) {
                videoRef.current.srcObject = stream;
                videoRef.current.setAttribute('playsinline', true);
                await videoRef.current.play();
            }
            codeReader.current = new BrowserMultiFormatReader();
            codeReader.current.decodeFromVideoElementContinuously(videoRef.current, (result, err) => {
                if (result) {
                    alert(':', result.getText());
                    handleResult(result.getText());
                    stopScanner(); // 
                }
            });
        } catch (error) {
            console.error(':', error);
        }
    };
    const stopScanner = () => {
        codeReader.current?.reset();
        streamRef.current?.getTracks().forEach(track => track.stop());
        videoRef.current && (videoRef.current.srcObject = null);
    };

    const frameStyle = {
        position: 'absolute',
        top: '25%',
        left: '15%',
        width: '70%',
        height: '50%',
        border: '2px solid rgb(220, 2, 2)',
        boxSizing: 'border-box',
        zIndex: 3,
    }

    return (
        <Box className="video-container">
            <video ref={videoRef}
                playsInline
                autoPlay
                width="100%"
                height="auto"
            ></video>
            <div className="overlay">
                {/*  */}
                <div className="mask  topMask" />
                <div className="mask bttomMask" />
                <div className="mask leftMask" />
                <div className="mask rightMask" />
                {/*  */}
                <div style={frameStyle}>
                    <div className="scanLine"></div>
                </div>
            </div>
        </Box>
    );
};

const QrCodeReader = () => {
    const [open, setOpen] = useState(false);

    const handleResult = (result) => {
        setOpen(false);
        const url = result.split("query=")[1];
        alert(url)
    };

    return (
        <Box>
            <Box>
                <Button style={{ padding: 0 }} onClick={() => setOpen(true)}>
                    {/* <img src={scanIcon} width={22} style={{ marginRight: 6 }} alt="" /> */}
                    <span></span>
                </Button>
            </Box>
            <Model title="" open={open} handleClose={() => setOpen(false)}>
                <Scan handleResult={handleResult} open={open} />
            </Model>
        </Box>
    );
};

export default QrCodeReader;
